        import Vue from 'vue'
        import Router from 'vue-router'

        Vue.use(Router);
        import Home from  '../components/Home.vue'
        import Cart from  '../components/Cart.vue'
        import Thank from  '../components/Thank.vue'
        import User from  '../components/User.vue'

        import Checkout from  '../components/Checkout.vue'
        import Notfound from '../components/Notfound.vue'
        var  Bar = { name:"Bar" ,template: '<div>bar</div>' }
        console.log(Bar);
        Vue.component('foo', {
          template: '<div>A custom component!</div>'
        })

        const routes = [
                {
                    path: '/',
                    name: 'Home',
                    children: [{
                            path: 'child',
                            component: User
                        }],
                    component: Home
                }, {
                    path: '/cart',
                    name: 'Cart',
                    component: Cart
                },
                {
                    path: '/checkout',
                    name: 'Checkout',
                    component: Checkout
                },
                , {
                    path: '/thank',
                    name: 'Thank',
                    component: Thank
                },
                {
                    path: '/user/:id',
                    component: User
                },
                {name: 'notfound',
                 path:'/notfound',
                 component:Notfound
                },
                { path: '/*',
                    redirect: {name: 'notfound'}
                }
            ];
        export default new Router({
                    routes:routes
           })




