/**
* @component : User.vue 
* @author webdev
* Date : Feb 16, 2017  6:07:47 PM  
*/

<template>
    <div id="component_id">
        <div class="user"> {{api}}  
            <pre>{{error|json}}</pre>
            <h2>{{msg}} User child {{ $route.params.id }}</h2>
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
    export default {
        name: "User",
        data: function () {
            return {
                msg: 'Welcome!',
                api: null,
                apiresp: {id: 1},
                error:{}
            }
        },
        methods: {
            setData() {
                this.msg = "Welcome again"

            },
            getData() {
                this.$http.get('/api/call1').then((response) => {
                    this.api = response.data.message
                }, (response) => {
                    this.error = response.response.data
                })
            }
        },
        created: function () {
            this.setData();
            this.getData();
        },
    }
</script>
<style>
    #component_id { }
</style>



